.cursor-axe {
    cursor: url('\png-transparent-axe-hatchet-ico-icon-bloody-ax-cartoon-ax-icon-design Kopie.png'), auto;
  }

  .tree {
    width: 100px;
    height: 120px;
    background: #4a7c59;
    position: relative;
    border-radius: 0 0 50px 50px;
}

.tree::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 20px;
    height: 30px;
    background: #8B4513;
    border-radius: 5px;
}

.tree-top {
    position: absolute;
    top: -40px;
    left: 0;
    width: 100%;
    height: 80px;
    background: #2ecc71;
    border-radius: 50% 50% 0 0;
}

.cursor-crosshair {
  cursor: crosshair;
}

@keyframes fly {
  0% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(100px, -50px);
  }
  50% {
    transform: translate(-50px, 100px);
  }
  75% {
    transform: translate(75px, 50px);
  }
  100% {
    transform: translate(0, 0);
  }
}

.bird-container {
  position: absolute;
  width: 60px;
  height: 40px;
  animation: fly 30s linear infinite;
  animation-delay: calc(var(--bird-index) * -2s);
  left: var(--start-x);
  top: var(--start-y);
}

.bird-info {
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  text-align: center;
}

.bird {
  width: 100%;
  height: 100%;
  position: relative;
  cursor: crosshair;
}

.bird-body {
  width: 30px;
  height: 20px;
  background: #4a7c59;
  border-radius: 50% 50% 20% 20%;
  position: absolute;
  top: 10px;
  left: 15px;
}

.wing {
  width: 20px;
  height: 30px;
  background: #2ecc71;
  position: absolute;
  top: 0;
  border-radius: 50% 50% 20% 20%;
  transform-origin: top center;
  animation: flap 0.5s ease-in-out infinite alternate;
}

.left-wing {
  left: 5px;
}

.right-wing {
  right: 5px;
}

@keyframes flap {
  0% {
    transform: rotateX(0deg);
  }
  100% {
    transform: rotateX(60deg);
  }
}

.bird-tail {
  width: 15px;
  height: 10px;
  background: #4a7c59;
  position: absolute;
  bottom: -5px;
  left: 22.5px;
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

.bird-beak {
  width: 10px;
  height: 5px;
  background: #f39c12;
  position: absolute;
  top: 15px;
  left: 5px;
  clip-path: polygon(0% 50%, 100% 0%, 100% 100%);
}

.bird-eye {
  width: 4px;
  height: 4px;
  background: #000;
  border-radius: 50%;
  position: absolute;
  top: 12px;
  left: 10px;
}

.cursor-none {
  cursor: none;
}